import { yupResolver } from "@hookform/resolvers/yup";
import GoogleIcon from "@mui/icons-material/Google";
import { Button, Stack, TextField, Typography } from "@mui/material";
import ButtonGroup from "@mui/material/ButtonGroup";
import Divider from "@mui/material/Divider";
import { Box } from "@mui/system";
import { setGlobalAccessToken } from "modules/login/context/accessToken";
import { useAuthContext } from "modules/login/context/authContext";
import { auth, AuthInputs } from "modules/login/loginFetch";
import { setRefreshToken } from "modules/login/token";
import { Controller, useForm } from "react-hook-form";
import { useTranslation } from "react-i18next";
import { useMutation } from "react-query";
import { useNavigate } from "react-router-dom";
import * as yup from "yup";

import logo from "../../other/images/logo.png";
type LoginFormFields = {
  email: string;
  password: string;
};

const Login = () => {
  const schema = yup.object().shape({
    email: yup.string().required(),
    password: yup.string().required(),
  });
  const { setCheckAuth, setUserInfo } = useAuthContext();
  const navigate = useNavigate();
  const { t, i18n } = useTranslation();
  const changeLanguage = (lng: string) => {
    i18n.changeLanguage(lng);
  };
  const {
    control,
    handleSubmit,
    formState: { errors },
  } = useForm<LoginFormFields>({
    defaultValues: {
      email: "",
      password: "",
    },
    resolver: yupResolver(schema),
  });

  const $login = useMutation(({ email, password }: AuthInputs) =>
    auth({ email, password })
  );

  const onSubmit = (data: LoginFormFields) => {
    const { email, password } = data;
    $login.mutate(
      { email, password },
      {
        onSuccess: (data) => {
          setRefreshToken(data.refreshToken);
          setGlobalAccessToken(data.accessToken);
          setCheckAuth(data.accessToken);
          const user: {
            name: string;
            email: string;
            picture: string;
            role: string;
            _id: string;
          } = data.user;
          setUserInfo(user);
          setCheckAuth(data.refreshToken);
          user.role === "user" && navigate("/main");
          user.role === "Admin" && navigate("/quizlist");
          user.role === "Super" && navigate("/adminlist");
        },
      }
    );
  };
  return (
    <>
      <Stack
        justifyContent="center"
        alignItems="center"
        height="100vh"
        width={1}
        sx={{
          background:
            " linear-gradient(45deg, rgba(140,17,193,1) 0%, rgba(68,63,224,1) 47%, rgba(17,171,203,1) 100%)",
        }}
      >
        <Box component="img" src={logo} sx={{ width: 100, mb: 5 }}></Box>
        <Stack
          justifyContent="center"
          width={1}
          alignItems="center"
          spacing={2}
          sx={{
            background: "white",
            width: "100%",
            maxWidth: "300px",
            borderRadius: 1,
            p: "20px 20px 50px 20px",
          }}
        >
          <Stack>
            <ButtonGroup size="small">
              <Button
                variant={
                  localStorage.getItem("i18nextLng") === "en"
                    ? "contained"
                    : "outlined"
                }
                onClick={(e) => changeLanguage("en")}
              >
                EN
              </Button>
              <Button
                variant={
                  localStorage.getItem("i18nextLng") === "ru"
                    ? "contained"
                    : "outlined"
                }
                onClick={(e) => changeLanguage("ru")}
              >
                RU
              </Button>
            </ButtonGroup>
          </Stack>
          <Typography variant="h1">{t("login.login")}</Typography>
          <Box
            sx={{
              width: "100%",
            }}
          >
            <form onSubmit={handleSubmit(onSubmit)}>
              <Stack direction="column" spacing={2}>
                <Controller
                  control={control}
                  name="email"
                  render={({ field }) => (
                    <TextField
                      fullWidth
                      label={t("login.email")}
                      type="text"
                      required
                      focused
                      error={!!errors.email}
                      {...field}
                    />
                  )}
                />
                <Controller
                  control={control}
                  name="password"
                  render={({ field }) => (
                    <TextField
                      fullWidth
                      label={t("login.password")}
                      type="password"
                      required
                      focused
                      error={!!errors.password}
                      {...field}
                    />
                  )}
                />
                <Stack direction="row" gap={1}>
                  <Button fullWidth type="submit" disabled={$login.isLoading}>
                    {t("login.submit")}
                  </Button>

                  <Button
                    variant="text"
                    fullWidth
                    color="secondary"
                    onClick={() => navigate("/signup")}
                  >
                    {t("login.register")}
                  </Button>
                </Stack>
              </Stack>
            </form>
          </Box>
          <Box width={1}>
            <Divider sx={{ bgcolor: "black" }} />
          </Box>
          <Box width={1}>
            <Button
              // variant="outlined"
              color="secondary"
              onClick={() => {
                window.location.href = `${process.env.REACT_APP_LOCAL_URL1}/auth/google`;
              }}
              fullWidth
              sx={{
                display: "flex",
                alignItems: "center",
                gap: 1,
                px: 3,
                py: 1.5,
              }}
            >
              <GoogleIcon />
              <Typography fontWeight={600}>{t("login.glogin")}</Typography>
            </Button>
          </Box>
        </Stack>
      </Stack>
    </>
  );
};
export default Login;
